"use client";

// src/components/GoogleMap.jsx
import React, { useMemo, useState } from 'react';
import { GoogleMap, LoadScript, Marker } from '@react-google-maps/api';
import { Loader2 } from 'lucide-react';

const containerStyle = {
  width: '100%',
  height: '100%',
  minHeight: '200px',
};

// ÏÑúÏö∏ Í∏∞Î≥∏ Ï¢åÌëú (Í∞ïÎÇ®Íµ¨)
const defaultCenter = {
  lat: 37.4979,
  lng: 127.0276,
};

// ÏúÑÏπò Î¨∏ÏûêÏó¥ÏùÑ Í∞ÑÎã®Ìûà ÌååÏã± (Ïòà: "Gangnam, Seoul" -> Í∞ïÎÇ®Íµ¨ Ï¢åÌëú)
const parseLocation = (location) => {
  if (!location) return defaultCenter;
  
  const lower = location.toLowerCase();
  // Í∞ÑÎã®Ìïú ÏúÑÏπò Îß§Ìïë (Ï∂îÌõÑ Geocoding APIÎ°ú Í∞úÏÑ† Í∞ÄÎä•)
  if (lower.includes('gangnam') || lower.includes('Í∞ïÎÇ®')) {
    return { lat: 37.4979, lng: 127.0276 };
  }
  if (lower.includes('sinsa') || lower.includes('Ïã†ÏÇ¨')) {
    return { lat: 37.5161, lng: 127.0193 };
  }
  if (lower.includes('seoul') || lower.includes('ÏÑúÏö∏')) {
    return { lat: 37.5665, lng: 126.9780 }; // ÏÑúÏö∏ÏãúÏ≤≠
  }
  
  return defaultCenter;
};

export const GoogleMapComponent = ({ location, hospitalName, latitude, longitude }) => {
  const apiKey =
    (typeof process !== "undefined" &&
      process.env?.NEXT_PUBLIC_GOOGLE_MAPS_API_KEY) ||
    (typeof import.meta !== "undefined" && import.meta.env
      ? import.meta.env.VITE_GOOGLE_MAPS_API_KEY
      : undefined);

  // Ï¢åÌëúÍ∞Ä ÏûàÏúºÎ©¥ Ïö∞ÏÑ† ÏÇ¨Ïö©, ÏóÜÏúºÎ©¥ Ï£ºÏÜå ÌååÏã±
  const center = useMemo(() => {
    if (latitude && longitude) {
      return { lat: Number(latitude), lng: Number(longitude) };
    }
    return parseLocation(location);
  }, [location, latitude, longitude]);

  // API ÌÇ§Í∞Ä ÏóÜÏúºÎ©¥ placeholder ÌëúÏãú
  if (!apiKey) {
    return (
      <div className="bg-gray-100 w-full h-full min-h-[200px] flex items-center justify-center text-gray-400 font-bold">
        <div className="flex flex-col items-center gap-2">
          <span className="text-xs">Google Maps API key required</span>
          <span className="text-[10px] text-gray-300">
            Add NEXT_PUBLIC_GOOGLE_MAPS_API_KEY (or VITE_GOOGLE_MAPS_API_KEY) to
            .env
          </span>
        </div>
      </div>
    );
  }

  const [mapLoaded, setMapLoaded] = useState(false);

  return (
    <LoadScript 
      googleMapsApiKey={apiKey}
      onLoad={() => setMapLoaded(true)}
    >
      {mapLoaded ? (
        <GoogleMap
          mapContainerStyle={containerStyle}
          center={center}
          zoom={15}
          options={{
            disableDefaultUI: false,
            zoomControl: true,
            streetViewControl: false,
            mapTypeControl: false,
            fullscreenControl: true,
            styles: [
              {
                featureType: 'poi',
                elementType: 'labels',
                stylers: [{ visibility: 'off' }],
              },
            ],
          }}
        >
          <Marker
            position={center}
            title={hospitalName || 'Hospital Location'}
            icon={{
              url: 'https://maps.google.com/mapfiles/ms/icons/red-dot.png',
            }}
          />
        </GoogleMap>
      ) : (
        <div className="w-full h-full min-h-[200px] flex items-center justify-center bg-gray-100">
          <div className="flex flex-col items-center gap-2">
            <Loader2 size={24} className="animate-spin text-teal-600" />
            <span className="text-xs text-gray-500">Loading map...</span>
          </div>
        </div>
      )}
    </LoadScript>
  );
};
