// 1. Ïù¥ÎØ∏ÏßÄ Îç∞Ïù¥ÌÑ∞ Ï†ïÍ∑úÌôî (Î¨¥Ï°∞Í±¥ Ïú†Ìö®Ìïú URL Î∞∞Ïó¥Î°ú Î∞òÌôò)
export const normalizeImages = (raw) => {
  if (!raw) return [];
  
  // Ïù¥ÎØ∏ Î∞∞Ïó¥Ïù¥Î©¥ Ïú†Ìö®Ìïú Í∞í(URL)Îßå ÎÇ®ÍπÄ
  if (Array.isArray(raw)) return raw.filter(Boolean);

  // Î¨∏ÏûêÏó¥Ïù∏ Í≤ΩÏö∞ (JSON Ïä§Ìä∏ÎßÅÏù¥Í±∞ÎÇò Îã®Ïùº URL Ï≤òÎ¶¨)
  if (typeof raw === "string") {
    const t = raw.trim();
    
    // JSON Î∞∞Ïó¥ ÌòïÌÉúÏù∏ Í≤ΩÏö∞ ("[...]")
    if (t.startsWith("[") && t.endsWith("]")) {
      try {
        const parsed = JSON.parse(t);
        if (Array.isArray(parsed)) return parsed.filter(Boolean);
      } catch (e) {
          console.error("Image parse error:", e);
      }
    }
    
    // Í∑∏ÎÉ• httpÎ°ú ÏãúÏûëÌïòÎäî Îã®Ïùº URLÏù∏ Í≤ΩÏö∞
    if (t.startsWith("http")) return [t];
  }
  
  return [];
};

// 2. Î≥ëÏõê Îç∞Ïù¥ÌÑ∞ Î≥ÄÌôò (DB -> UI ÌëúÏ§Ä)
export const mapHospitalRow = (h) => {
  if (!h) return null; // üî• ÏïàÏ†ÑÏû•Ïπò: Îç∞Ïù¥ÌÑ∞ ÏóÜÏúºÎ©¥ ÌÑ∞ÏßÄÏßÄ ÏïäÍ≥† null Î∞òÌôò

  return {
    id: h.id,
    slug: h.slug ?? null,
    name: h.name,
    location: h.location ?? h.location_en ?? h.location_kr ?? '',
    address_detail: h.address_detail ?? '',
    description: h.description,
    tags: Array.isArray(h.tags) ? h.tags : [], // ÌÉúÍ∑∏Í∞Ä Î∞∞Ïó¥Ïù¥ ÏïÑÎãàÎ©¥ Îπà Î∞∞Ïó¥Î°ú
    rating: h.rating ?? 0, // Í∞ÄÏßú Îç∞Ïù¥ÌÑ∞(4.8) Ï†úÍ±∞ -> 0ÏúºÎ°ú Ï¥àÍ∏∞Ìôî
    reviewsCount: h.reviews_count ?? 0, // UI ÌïÑÎìúÎ™Ö Î™ÖÌôïÌôî
    images: normalizeImages(h.images), // Ïù¥ÎØ∏ÏßÄ Ï†ïÍ∑úÌôî Ï†ÅÏö©
    latitude: h.latitude ?? null,
    longitude: h.longitude ?? null,
    operating_hours: h.operating_hours ?? null,
    doctorProfile: h.doctor_profile || null,
  };
};

// 3. ÏãúÏà† Îç∞Ïù¥ÌÑ∞ Î≥ÄÌôò (DB -> UI ÌëúÏ§Ä)
export const mapTreatmentRow = (t) => {
  if (!t) return null; // üî• ÏïàÏ†ÑÏû•Ïπò

  return {
    id: t.id,
    slug: t.slug ?? null,
    title: t.name,
    desc: t.description,
    fullDescription: t.full_description,
    hospitalId: t.hospital_id,
    price: t.price_min ? `$${t.price_min.toLocaleString()}` : "Inquire", // Í∞ÄÍ≤© Ìè¨Îß∑ÌåÖ
    tags: Array.isArray(t.tags) ? t.tags : [],
    images: normalizeImages(t.images), // Ïù¥ÎØ∏ÏßÄ Ï†ïÍ∑úÌôî Ï†ÅÏö©
    benefits: Array.isArray(t.benefits) ? t.benefits : [],
    
    // JoinÎêú Î≥ëÏõê Ï†ïÎ≥¥Í∞Ä ÏûàÎã§Î©¥ Îß§Ìïë (ÏóÜÏúºÎ©¥ Í∏∞Î≥∏Í∞í)
    hospitalName: t.hospitals?.name || "Partner Hospital",
    hospitalSlug: t.hospitals?.slug || null,
    hospitalLocation:
      t.hospitals?.location ||
      t.hospitals?.location_en ||
      t.hospitals?.location_kr ||
      "Seoul, Korea",
  };
};